import {Component, OnInit} from '@angular/core';
import {ProductService} from "../../services/product.service";
import {ProductModelServer, serverResponse} from "../../models/product.model";
import {CartService} from "../../services/cart.service";
import {Router} from "@angular/router";
import { SwiperOptions } from 'swiper';

@Component({
  selector: 'mg-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})


export class HomeComponent implements OnInit {
  config: SwiperOptions = {
    slidesPerView: 4,
    pagination: { el: '.swiper-pagination', clickable: true },
    autoHeight: true,
    allowTouchMove: true,
    autoplay: {
      delay: 6000,
      disableOnInteraction: true
    },
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev'
    },
    loop: false
  };

  products: ProductModelServer[] = [];
  colors: any[] = [];
  colors_src: any[] = [];

  constructor(private productService: ProductService,
              private cartService: CartService,
              private router:Router) {
  }

  ngOnInit() {


    this.productService.getAllProducts(15).subscribe((prods: serverResponse) => {
      this.products = prods.products;
      prods.products.forEach(prod => {
        if(prod.colors != null){
          //console.log(prod);
          this.colors = prod.colors.split(';');
          this.colors_src = prod.color_img.split(';');
        }
      })

      console.log(this.colors, this.colors_src);
    });

  }

  ngAfterViewInit(): void {

  }

  AddProduct(id: Number) {
    this.cartService.AddProductToCart(id);
  }

  selectProduct(id: Number) {
    this.router.navigate(['/product', id]).then();
  }

  scrollDown(){
    let scrollToTop = window.setInterval(() => {
      let pos = window.pageYOffset;
      if (pos == 0) {
            window.scrollTo(0, 650);
        } else {
            window.clearInterval(scrollToTop);
        }
    }, 160);
  }

  updateSource(source, i){
    i = i - 1;
    //console.log(source, this.products[i]);
    this.products[i].image = source;
  }

}
